import React, { useState } from 'react'
import screen1 from './img_CrashBandicoot/ss16.jpg'
import screen2 from './img_CrashBandicoot/ss20.jpg'
import screen3 from './img_CrashBandicoot/ss6.jpg'
import screen4 from './img_CrashBandicoot/ss7.jpg'
import screen5 from './img_CrashBandicoot/ss1.jpg'
import screen6 from './img_CrashBandicoot/ss2.jpg'
import screen7 from './img_CrashBandicoot/ss3.jpg'
import logo from './img_CrashBandicoot/logo_CrashBandicoot.png'
import "../../style/components/crashBandicootPage.css"

const CrashBandicootPage = () => {

    return (
        <div>
            <div className='artykul'>
                <img id='logoCBF' src={logo} alt={"źródło: samemu okroiłem z okładki"} title={"Tak wygląda logo gry"}></img>
                <p lang='pl'>Można ją z łatwością przejść w dwie godziny, ale zdobycie stu procent wymaga nie lada odwagi, wytrzymałości oraz szczęścia. Tak oto widzę pierwszą produkcję studia Naughty Dog na PlayStation.</p>
                <img className='image' src={screen1} alt={"źródło: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Ta plansza niszczyła dzieciństwo"}></img>
                <p lang='pl'>Tę odsłonę charakteryzuje to, że można ją jednocześnie kochać i nienawidzić. Gra jest piękna! W 1996 roku na PlayStation nie wyszło dużo platformówek w trzech wymiarach np. Bubsy 3D, Pandemonium! czy Cheesy, które wyszły różnie jak i wyglądały inaczej. Poza tym, inne tytuły z tego roku zostały przez Crasha zmiażdżone jak tytułowy jamraj przez kamienne bloki. Zasadą tej gry jest pokonanie wszystkich plansz na trzech wyspach, nie tracąc przy tym wszystkich żyć.</p>
                <img className='image' src={screen2} alt={"źródło: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"To był Hulk mojego dzieciństwa"}></img>
                <p lang='pl'>Plansze są równie ciekawe jak sama oprawa graficzna. Przyjdzie nam się tutaj wspinać po ogromnych palisadach wypełnionych przeciwnikami, będziemy uciekać przed toczącą się kulą, przemierzać leśne ścieżki, zarośnięte złowrogimi rosiczkami, laboratoria złego naukowca które są wypełnione po brzegi robotami i laserami, przemierzać w chmurach dziurawe, drewniane mosty lub wspinać się po ścianie zamczyska w trakcie burzy, podczas, gdy krople deszczu zalewają kamienne bloki, czyniąc je bardzo śliskimi. Wszystkie te plansze są bardzo mądrze poukładane i każda z nich ma w sobie pewnego rodzaju wyzwanie.</p>
                <img className='image' src={screen3} alt={"źródło: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Inni nienawidzili plansz rzecznych, ja kocham do dzisiaj"}></img>
                <p lang='pl'>Jak wspomniałem w pierwszym akapicie, grę można ukończyć „bez wysiłku” w 2h, ale prawdziwa zabawa zaczyna się przy próbie przejścia jej w pełni. Wyzwaniem jest zniszczenie wszystkich skrzyń na planszy i w bonusowych ich pomieszczeniach, nie tracąc przy tym ani jednego życia. Dodajcie do tego jeszcze sterowanie, do którego trzeba przywyknąć, wraz z dużą ilością skakania, po czasami bardzo wymagających, platformach.</p>
                <img className='image' src={screen4} alt={"źródło: https://psxdatacenter.com/games/P/L/SLES-00613.html"} title={"Scena jak z animowanego Indiany Dżonsa"}></img>
                <p lang='pl'>Za każdą udaną próbę przejścia poziomu bonusowego, od naszej dziewczyny Tawny, której swoją drogą uratowanie jest naszym głównym celem w grze, otrzymujemy możliwość jej zapisania (gry, nie dziewczyny) na karcie pamięci lub otrzymujemy sekwencję przycisków do wczytania. Zapisać grę możemy jeszcze w momencie gdy zbierzemy jakiś kryształ, który otrzymujemy za zniszczenie wszystkich skrzyń. Wydaje się to łatwe - bynajmniej - nie jest na ostatniej, trzeciej wyspie. Przykładowo wszystkim znany poziom Slippery Climb ma w sobie bodajże tylko dwa? <abbr title='Punkty zapisu lub miejsce do którego wrócimy po śmierci'>Checkpointy</abbr> porozrzucane przy każdym końcu ściany. </p>
                <img className='image' src={screen5} alt={"źródło: Własne"} title={"Tak! Mam kryształ w najtrudniejszej planszy"}></img>
                <p lang='pl'>Żeby tego było mało, gra też uniemożliwia nam nazbieranie sobie sporego zasobu żyć, gdyż samo zapisanie gry nie zapamiętuje ich. Po każdym wczytaniu gry, liczba żyć spada do czterech i musimy zazwyczaj wracać do pierwszych plansz, aby je na nowo nazbierać.</p>
                <p>Jak widać kultowa produkcja Naughty Dog się z nami nie cacka i cały czas rzuca kłody pod nogi, jak na planszy w starych ruinach. Możemy oczywiście nabrać wprawy i jak na przykład ja (skromnym zdaniem), przechodzić niektóre levele bez problemu, po prostu pamiętając, co nas czeka zawczasu.
                </p>
                <img className='image' src={screen6} alt={"źródło: Własne"} title={"Widać w tle pozostałe 3 wysypy"}></img>
                <p lang='pl'>Takie zapamiętywanie przydaje się również w walkach z bossami, które pojawiają się co kilka poziomów. Wszyscy mają swój niezmieniony schemat, który warto zapamiętać, żeby z łatwością zmniejszać ich paski życia do zera absolutnego… Tak jakby, gdyż z częścią z nich staniemy w szranki w późniejszych odsłonach Crasha.</p>
                <img className='image' src={screen7} alt={"źródło: Własne"} title={"Ujeżdżanie dzika, to jest TO!!!"}></img>
                <p>Crash Bandicoot jest tak samo smaczny jak owoce Wumpa, posmakujesz jak zagrasz, mogą one być dla Ciebie słodkie, z lekką goryczką lub w ogóle niestrawne - wszystko zależy od Twojej tolerancji na wymagające gry, cierpliwości i chęci uczenia się schematów, poprzez wielokrotne ich powtarzanie.</p>
                <div className='tabela'>
                    <table id='tabelaInfo'>
                        <tr>
                            <td>Deweloper</td>
                            <td>Naughty Dog</td>
                        </tr>
                        <tr>
                            <td>Wydawca</td>
                            <td>Sony</td>
                        </tr>
                        <tr>
                            <td>Data wydania</td>
                            <td>15 listopada 1996</td>
                        </tr>
                        <tr>
                            <td>Numer Seryjny</td>
                            <td>SCES-00344</td>
                        </tr>
                    </table>
                </div>
                <div>
                    {/* <img></img>
                <img></img> */}
                </div>
            </div>
        </div>
    )
}

export default CrashBandicootPage