import React from 'react'
import { Link } from 'react-router-dom';
import screen1 from './img_NinjaShadowOfDarkness/ss1.jpg'
import screen2 from './img_NinjaShadowOfDarkness/ss2.jpg'
import screen3 from './img_NinjaShadowOfDarkness/ss3.jpg'
import screen4 from './img_NinjaShadowOfDarkness/ss4.jpg'
import screen5 from './img_NinjaShadowOfDarkness/ss5.jpg'
import screen6 from './img_NinjaShadowOfDarkness/ss6.jpg'
import screen7 from './img_NinjaShadowOfDarkness/ss7.jpg'
import logo from './img_NinjaShadowOfDarkness/logo.png'
import "../../style/components/ninjaShadowOfDarknessPage.css"

const NinjaShadowOfDarknessPage = () => {

    return (
        <div>
            <div className='artykul'>
                <img id='logoCBF' src={logo} alt={"źródło: https://images.launchbox-app.com/3298b017-6ef5-4844-bcc4-a22e8db5da81.png"} title={"Tak wygląda logo gry"}></img>
                <p lang='pl'><abbr title='Inny gra studia, która odgrywała się w czasach współczesnych'>Fighting Force</abbr> w feudalnej Japonii z elementami platformowymi wyciągniętymi z pierwszego <abbr title='Przejdź do recenzji pierwszego Crasha'><Link to='../crashbandicoot'>Crasha</Link></abbr>, gdzie prowadzisz wojownika Ninja do walki przeciwko smokom lub demonicznym plugawstwom – tak mogę opisać w jednym zdaniu <b>Ninja: Shadow of Darkness</b> od Core Design</p>
                <img className='image' src={screen1} alt={"źródło: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Najgorsze połączenie: wąski most i dużo przeciwników"}></img>
                <p lang='pl'>Bardzo długo opierałem się tej grze, gdyż utkwiła ona w mej pamięci jako bardzo wymagającą. Po tylu latach, mając za sobą ukończone gry od <abbr title='Twórcy takiej serii jak Dark Souls lub gier jak: Bloodborne czy Elden Ring'>From Software</abbr>, byłem przekonany że jestem na nią gotowy… okazało się jednak że to była bardzo nieprzemyślana decyzja. Gra jest piekielnie trudna, wymagająca i wyciskająca z nas siódme poty. Nie wybacza ona błędów i wręcz wymaga od nas chirurgicznej precyzji w przechodzeniu plansz</p>
                <img className='image' src={screen2} alt={"źródło: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Mała platforma, śmiercionościna woda wkoło... czego chcieć więcej?"}></img>
                <p lang='pl'>Problemem nie jest tutaj system walki, gdyż opiera się on na dwóch przyciskach, odpowiadających za ataki pięściami lub nogami. Mamy też blok i dwa dodatkowe ataki specjalne. Poza tym, mamy jeszcze system zwiększania siły ataków i czarów poprzez specjalne, poukrywane po planszach zwoje. Oczywiście możemy znaleźć bronie, które bardzo ułatwiają nam ubijanie czortów. Jak jesteśmy leniwi, to po każdej planszy możemy za zebrane monety kupić broń, dodatkową moc lub życia.</p>
                <img className='image' src={screen3} alt={"źródło: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"W sklepie możemy sobie kupić udko, broń i cenne życie"}></img>
                <p lang='pl'>Niby bardzo fajna opcja, ale wystarczy tylko jeden błąd, jedno potknięcie lub zbyt duża ilość wrogów naraz, a nasze zebrane lub kupione bronie, wraz z jednym poziomem mocy, przepadają bezpowrotnie, co niestety powoduje że przeciwników ubijamy dłużej, a to z kolei czyni grę trudniejszą. <b>Czyli ginąc raz, giniemy częściej, a ginąc częściej gra robi się nie do przejścia.</b> (w późniejszych poziomach)</p>
                <img className='image' src={screen4} alt={"źródło: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Ciemno wszędzie, głucho wszędzie... przeciwników pełno"}></img>
                <p lang='pl'>Plansze są bardzo urozmaicone, gdyż przyjdzie nam się zmagać z wrogami w deszczowych lasach, mrocznych jaskiniach, słonecznych plażach, na piekielnych otchłaniach zalanych lawą kończąc. Każde mają swój unikatowy klimat i zestaw przeciwników. Poza tym, na deser, mamy na końcu każdego <abbr title='inaczej plansza, poziom'>levelu</abbr> walkę z <abbr title='ostatni przeciwnik, zazwyczaj bardzo trudny'>bossem</abbr>. Ich trudność jest różna, niektórych możemy zgładzić zwykłym <abbr title='inaczej, bardzo szybkie przyciskanie przycisku'>mashowaniem</abbr> przycisku ataku, a pozostali wymagają od nas nauczenia się ich ruchów, byśmy z tej walki wyszli cało.</p>
                <img className='image' src={screen5} alt={"źródło: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Wydepilowany Tauren stoi nam na drodze ukończenia gry"}></img>
                <p lang='pl'>Graficznie ten tytuł przedstawia się bardzo sympatycznie, levele są bardzo ładnie wymodelowane i zaprojektowane. Ścieżka dźwiękowa buduje bardzo skutecznie nastrój na zabijanie przeciwników, zwiedzanie i podziwianie świata wykreowanego przez twórców.</p>
                <img className='image' src={screen6} alt={"źródło: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Panowie nie chcą ze mną lepić bałwana"}></img>
                <p lang='pl'><b>Gra wymęczyła mnie niezmiernie!</b> Napis „Game Over” im dalej w las, tym pojawiał się coraz częściej. Żyć nie uda nam się wyfarmić zbyt dużo, bo gra ma limit posiadania ich tylko dziewięciu i (chyba) czterech <abbr title='czyli opcja kontynuowania gry, z utratą wszystkich postępów poza lokalizacją'>„Continue”</abbr>. Sposobem na przejście Ninja: Shadow of Darkness, jest nauczenie się plansz i znajdowanie sekretów, które chowają się za specjalnymi ścianami lub w ukrytych przejściach. Po kilkunastu godzinach udało mi się ukończyć ten tytuł. Dokonałem tego przechodząc go w sposób następujący - starałem się kończyć level, nie tracąc w nim życia (było to niemożliwe już na trzech ostatnich poziomach). Zadanie to było bardzo trudne, ale uwierzcie mi, na ostatniej planszy oszczędność żyć, się opłaciła, bo końcowy „boss” był niemożliwie trudny i wyrywałem sobie na nim włosy</p>
                <img className='image' src={screen7} alt={"źródło: https://psxdatacenter.com/games/P/N/SLES-01554.html"} title={"Walka parawianów na plaży nie oszczędziła również tej gry"}></img>
                <p>Niestety po zakończeniu gry większą satysfakcję czerpałem z samego faktu jej ukończenia, niż z końcowej bardzo skromnej animacji.</p>
                <div className='tabela'>
                    <table id='tabelaInfo'>
                        <tr>
                            <td id='pierwszaKolumna'>Deweloper</td>
                            <td>Core Designe</td>
                        </tr>
                        <tr>
                            <td id='pierwszaKolumna'>Wydawca</td>
                            <td>Eidos</td>
                        </tr>
                        <tr>
                            <td id='pierwszaKolumna'>Data wydania</td>
                            <td>30 września 1998</td>
                        </tr>
                        <tr>
                            <td id='pierwszaKolumna'>Numer Seryjny</td>
                            <td>SLES-01554</td>
                        </tr>
                    </table>
                </div>
                <div>
                    {/* <img></img>
                <img></img> */}
                </div>
            </div>
        </div>
    )
}

export default NinjaShadowOfDarknessPage